:root {
  --text: #10120d;
  --background: #fdfdfc;
  --primary-button: #5a4d6f;
  --secondary-button: #dccfdd;
  --accent: #292433;
}

@media (prefers-color-scheme: dark) {
  :root{
    --text: #fdfdfc;
    --background: #10120d;
    --primary-button: #5a4d6f;
    --secondary-button: #292433;
    --accent: #dccfdd;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html {
  color-scheme: light dark;
}

img {
  max-width: 100%;
  display: block;
}

body{
  padding: 2rem 0;
  background-color: var(--background);
  color: var(--text);
}

button{
  padding: 0.2rem 1rem;
  background-color: var(--primary-button);
  border-radius: 0.5rem;
  border:1px solid var(--primary-button);
}

input{
  border:none;
  border-block-end: 1px solid var(--accent);
  padding:0.2rem 1rem;
}

.App {
  display: grid;
  gap:1rem;
  place-content: center;
  text-align: center;
}

h2{
  border-bottom: 1px solid var(--accent);
}

.CV{
  padding:1rem;
  width: clamp(30vw, 20em, 100vw);
  aspect-ratio: 1/1.414;
  border: 2px solid var(--accent);
}

.CV>div{
  padding-bottom: 2rem;
}

.CV>div, .education>div, .experience>div{
  display:grid;
  gap:1rem;
}

.general{
  display:grid;
  grid-template-columns: 1fr 1fr;
}

.general>h2{
  grid-column: span 2;
}

.general>div{
  display:flex;
  justify-content: center;
  line-height: 0.9;
}

.education-item, .experience-item{
  display:grid;
  grid-template-columns: 3fr 1fr;
  text-align: left;
  gap:0 !important;
}

.education-item>div:first-child,
.experience-item>div:first-child{
  font-weight: bold;
  grid-column: span 2;
  color: var(--accent);
}

svg{
  margin: 0 0.5rem;
}

